.App {
  /* text-align: center; */
}
 
.App-header {
  background-color: blueviolet;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header > * {
  margin-block-start: 0.5em;
  margin-block-end: 0.75em;
}
  
.App-main {
  background-color: aliceblue;
  min-height: 85vh;
  /* min-width: 80vw; */
  padding: 30px;
}
.Prompt-input {
  display: flex;
  justify-content: center;
}
.Prompt-send-button {
  background-color: blueviolet;
  color:white;
}
.Prompt-response {
  display: flex;
  flex-direction: column;
  /* justify-content: flex-start; */
  width: 77vw;
  border: 1px solid blueviolet;
  border-radius: 2px;
  margin: auto;
  margin-top: 20px;
  padding: 10px;
  height: 65vh;
  overflow-y: auto;
}